import usePeripherySDKStore from '@cetus/stores/src/usePeripherySDKStore'

export default function () {
  const { peripherySdk } = usePeripherySDKStore()

  const dcaCloseOrderPayload = async params => {
    const tx = await peripherySdk.Dca.dcaCloseOrderPayload(params)
    return tx
  }
  return { dcaCloseOrderPayload }
}
