import useTransaction from '@/hooks/common/useTransaction'
import useDcaClaim from '@/hooks/dca/useDcaClaim'
import useGetDcaOrderHistory from '@/hooks/dca/useGetDcaOrderHistory'
import { Block, ErrorTips, SelectTab } from '@cetus/design'
import { Tab } from '@cetus/design/src/components/common/SelectTab'
import { useAccountBalance } from '@cetus/hooks'
import useExplorer from '@cetus/hooks/src/useExplorer'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { CommonTypeInfo, TransactionStatusType } from '@cetus/types'
import { HTextLabelBox, Icon, NoData, Pagination } from '@cetus/ui-kit'
import { bnToAmount, d, formatNumber, timeFormatUTC } from '@cetus/utils'
import { Box, Button, Center, HStack, HTMLChakraProps, Skeleton, Text, VStack } from '@chakra-ui/react'
import { ReactNode, useEffect, useMemo, useState } from 'react'

export default function OverviewOrders({
  isDetail,
  currentTabVal,
  orderInfo,
  handleRefresh,
  pageDirect
}: {
  pageDirect: boolean
  isDetail: boolean
  currentTabVal: string
  orderInfo: any
  handleRefresh: () => void
}) {
  const { isApp } = useWindowWidth()
  const { inCoin: sellCoin, outCoin: buyCoin } = orderInfo
  const { getDcaOrderHistory } = useGetDcaOrderHistory()
  const [direct, setDirect] = useState(pageDirect)
  const [rateDirect, setRateDirect] = useState(pageDirect)
  const orderHistoryPageSize = 10
  const [orderHistoryList, setOrderHistoryList] = useState([])
  const [total, setTotal] = useState(0)
  const [currentPage, setCurrentPage] = useState(1)
  const [orderHistoryListLoading, setOrderHistoryListLoading] = useState(false)

  const [currentTab, setCurrentTab] = useState<Tab>({
    label: 'Overview',
    value: 'overview'
  })
  const tabList: Tab[] = useMemo(() => {
    return [
      {
        label: 'Overview',
        value: 'overview'
      },
      {
        label: orderHistoryListLoading || Number(total) === 0 ? 'Orders' : `Orders (${total})`,
        value: 'orders'
      }
    ]
  }, [total])

  const handleChangeTab = (item: Tab) => {
    setCurrentTab(item)
    if (orderInfo?.orderID) {
      handleGetDcaOrderHistory(orderInfo?.orderID, orderHistoryPageSize, 0, false)
    }
  }

  useEffect(() => {
    if (isDetail && orderInfo?.orderID) {
      handleGetDcaOrderHistory(orderInfo?.orderID, orderHistoryPageSize, 0, false)
    }
  }, [orderInfo, isDetail])

  const handleCurrentPage = (page: number) => {
    console.log('üöÄ ~ handleCurrentPage ~ page:', page)
    const offset = (page - 1) * orderHistoryPageSize
    handleGetDcaOrderHistory(orderInfo?.orderID, orderHistoryPageSize, offset, false)
    setCurrentPage(page)
  }

  const handleGetDcaOrderHistory = async (orderId: string, limit: number, offset: number, loading: boolean = false) => {
    console.log('üöÄ ~ handleGetDcaOrderHistory ~ loading:', loading)
    setOrderHistoryListLoading(loading)
    const res = await getDcaOrderHistory({
      orderId,
      limit,
      offset
    })
    console.log('üöÄ ~ handleGetDcaOrderHistory ~ res?.list:', res, res[orderId])
    if (res[orderId]?.list) {
      setOrderHistoryList(res[orderId]?.list)
    }
    if (res[orderId]?.total) {
      setTotal(res[orderId]?.total)
    }
    setOrderHistoryListLoading(false)
    console.log('üöÄ ~ file: TestData.tsx:170 ~ handleGetDcaOrderHistory ~ res:', res)
  }

  const [isClaimLoading, setIsClaimLoading] = useState(false)
  const { dcaWithdrawPayload } = useDcaClaim()
  const { fetchCoinBalance } = useAccountBalance()
  const { signAndExecuteTransaction } = useTransaction()
  const toClaim = async () => {
    setIsClaimLoading(true)
    try {
      console.log('üöÄ ~ closeOrder ~ orderInfo:', orderInfo)
      const paramsArr: any = []
      const claimOrderParams = {
        order_id: orderInfo?.orderID,
        in_coin_type: orderInfo?.inCoin.coin_type,
        out_coin_type: orderInfo?.outCoin.coin_type
      }
      paramsArr.push(claimOrderParams)
      console.log('üöÄ ~ toClaim ~ claimOrderParams:', paramsArr)
      const tx = await dcaWithdrawPayload(paramsArr)
      const res = await signAndExecuteTransaction(tx, {
        getShowInfo: (status: TransactionStatusType) => {
          const info: CommonTypeInfo = {
            modalDescriptionText: `${orderInfo?.outCoin?.symbol} Claimed`,
            toastTitleText: `${orderInfo?.outCoin?.symbol} Claimed`
          }
          if (status === 'success') {
            info.toastDescriptionContent = ''
          }

          return info
        }
      })
      if (res) {
        console.log('üöÄ ~ toClaim ~ res:', res)
        // ÈáçÊñ∞ÊãøÊï∞ÊçÆ
        setTimeout(() => {
          handleRefresh()
          // fetchCoinBalance(currentAccount?.address, orderInfo?.outCoin.coin_type)
        }, 2000)
      }
      setIsClaimLoading(false)
    } catch (error) {
      console.log('üöÄ ~ toClaim ~ error:', error)
      setIsClaimLoading(false)
    }
  }
  const { getExplorerUrl } = useExplorer()
  return (
    <VStack w="100%" gap="20px">
      <SelectTab
        type="outlineTab"
        wrapStyle={{
          w: '100%',
          h: '36px',
          borderRadius: '8px',
          p: '4px',
          mt: '-1px',
          zIndex: '1'
        }}
        itemStyle={{
          w: '50%',
          fontSize: '12px'
        }}
        tabList={tabList}
        currentTab={currentTab.label}
        handleChangeTab={handleChangeTab}
      />
      <Block p="30px 8px 20px" mt="-42px" borderRadius="8px" zIndex="0" bg="bg_primary">
        {/* {currentTab.value == 'orders'  &&( */}
        {currentTab.value == 'orders' &&
          (orderHistoryListLoading ? (
            <Skeleton height="4" width="100%" />
          ) : orderHistoryList?.length === 0 ? (
            <NoData type="nodata" text="No orders" noBorder p="12px" bg="none" />
          ) : (
            <VStack w="100%" align="flex-start" gap="16px" mt="12px">
              {!isApp && (
                <HStack w="100%" flexDirection={{ base: 'column', lg: 'row' }}>
                  <DcaVLabel label="Order info" value="" w={{ base: '100%', lg: '40%' }} />
                  <DcaVLabel
                    label="Rate"
                    value=""
                    w={{ base: '100%', lg: 'calc(32% - 16px)' }}
                    direct={rateDirect}
                    onChangeDirect={() => setRateDirect(!rateDirect)}
                  />
                  <DcaVLabel label="Time (UTC)" value="" w={{ base: '100%', lg: '28%' }} align="flex-end" textAlign="right" />
                </HStack>
              )}
              {orderHistoryList?.map((orderHistory: any) => {
                console.log('üöÄ ~ {orderHistoryList?.map ~ orderHistory:', orderHistory)
                const inAmount = bnToAmount(orderHistory.inAmount, sellCoin?.decimals)
                const outAmount = bnToAmount(orderHistory.outAmount, buyCoin?.decimals)
                const rate = d(inAmount).div(outAmount).toString()
                const rateResever = d(1).div(rate).toString()
                return (
                  <HStack
                    w="100%"
                    key={orderHistory?.time}
                    flexDirection={{ base: 'column', lg: 'row' }}
                    borderBottom={isApp ? '1px solid' : 'none'}
                    pb={isApp ? '16px' : '0'}
                    borderColor="border"
                    _last={{
                      borderBottom: 'none',
                      pb: '0'
                    }}
                  >
                    <DcaVLabel
                      label={isApp ? 'Order info' : ''}
                      value={`${formatNumber(inAmount)} ${sellCoin.symbol} ‚Üí ${formatNumber(outAmount)} ${buyCoin.symbol}`}
                      w={{ base: '100%', lg: '40%' }}
                    />
                    <DcaVLabel
                      label={isApp ? 'Rate' : ''}
                      value={
                        rateDirect
                          ? `${formatNumber(rate)} ${sellCoin.symbol} per ${buyCoin.symbol}`
                          : `${formatNumber(rateResever)} ${buyCoin.symbol} per ${sellCoin.symbol}`
                      }
                      w={{ base: '100%', lg: 'calc(32% - 16px)' }}
                      direct={rateDirect}
                      showDirectIcon={isApp ? true : false}
                      onChangeDirect={() => setRateDirect(!rateDirect)}
                    />
                    <DcaVLabel
                      label={isApp ? 'Time (UTC)' : ''}
                      value={
                        <HStack h="12px">
                          <Text fontSize="12px" color="text_caption">
                            {timeFormatUTC(orderHistory?.time * 1000, 'YMDHM')}
                          </Text>
                          <Icon
                            xlinkHref="#icon-icon_link1"
                            onClick={() => {
                              window.open(getExplorerUrl(orderHistory?.tx, 'tx'))
                            }}
                          />
                        </HStack>
                      }
                      w={{ base: '100%', lg: '28%' }}
                      align="flex-end"
                      textAlign="right"
                    />
                  </HStack>
                )
              })}
              {d(total).gt(orderHistoryPageSize) && (
                <Center w="100%">
                  <Pagination total={total} size={orderHistoryPageSize} currentPage={currentPage} onChange={handleCurrentPage} />
                </Center>
              )}
            </VStack>
          ))}

        {currentTab.value == 'overview' && (
          <VStack align="flex-start" gap="20px">
            {orderInfo?.isShowTradeTips && orderInfo?.orderStatus == 'Active' && currentTabVal == 'active' && <WarningText />}
            <VStack w="100%" borderRadius="8px" p="12px" bg="card_bg" gap="12px">
              <HTextLabelBox
                isLoading={false}
                label={`DCA  ${sellCoin?.symbol} Balance`}
                value={`${formatNumber(orderInfo?.inBalance, 2) || '0'} ${sellCoin?.symbol}`}
                skeletonStyle={{
                  valueW: '128px'
                }}
              />
              <HStack w="100%" justify="space-between">
                <Text fontSize="12px">DCA {buyCoin?.symbol} Balance</Text>
                <HStack>
                  <Text color="text_caption" fontSize="12px">
                    {orderInfo?.outBalance > 0 ? formatNumber(orderInfo?.outBalance) : orderInfo?.outBalance} {buyCoin?.symbol}
                  </Text>
                  {currentTabVal == 'active' && (
                    <Button
                      isLoading={isClaimLoading}
                      onClick={toClaim}
                      disabled={orderInfo?.outBalance <= 0 || isClaimLoading}
                      w="60px"
                      h="20px"
                      fontSize="12px"
                      borderRadius="4px"
                      variant="outline"
                    >
                      Claim
                    </Button>
                  )}
                </HStack>
              </HStack>
              <HTextLabelBox
                isLoading={false}
                label="Amount Withdrawn"
                value={
                  currentTabVal == 'active'
                    ? `${formatNumber(orderInfo?.outWithdraw, 2)}`
                    : orderInfo?.orderStatus == 'PartialDeal' || orderInfo?.orderStatus == 'Close'
                      ? d(orderInfo?.outWithdraw).gt(0)
                        ? `${formatNumber(orderInfo?.inWithdrawn)} ${sellCoin?.symbol} ${formatNumber(orderInfo?.outWithdraw)} ${buyCoin?.symbol}`
                        : `${formatNumber(orderInfo?.inWithdrawn)} ${sellCoin?.symbol}`
                      : `${formatNumber(orderInfo?.outWithdraw)} ${buyCoin?.symbol}`
                }
                skeletonStyle={{
                  valueW: '128px'
                }}
              />
            </VStack>
            <HStack
              flexWrap="wrap"
              p={{ base: '0', lg: '0 12px' }}
              mt={{
                base: '-12px',
                lg: '-28px'
              }}
              sx={{
                '>div': {
                  mt: {
                    base: '12px',
                    lg: '28px'
                  },
                  w: {
                    base: '100%',
                    lg: 'calc((100% - 66px)/3)'
                  },
                  '&:nth-child(3n+1)': {
                    mr: {
                      base: '0',
                      lg: '50px'
                    }
                  }
                }
              }}
            >
              <DcaVLabel label="Invest Every" value={orderInfo?.investEvery} />
              <DcaVLabel label="# of Orders Left" value={orderInfo?.ofOrderLeft} />
              <DcaVLabel label="Each Order Size" value={`${orderInfo?.eachOrderSize} ${sellCoin?.symbol}`} align="flex-end" textAlign="right" />
              <DcaVLabel
                label="Minimum Price"
                value={
                  direct
                    ? `${orderInfo?.minPrice} ${sellCoin?.symbol} per ${buyCoin?.symbol}`
                    : `${orderInfo?.minPriceResever} ${buyCoin?.symbol} per ${sellCoin?.symbol}`
                }
                direct={direct}
                onChangeDirect={() => setDirect(!direct)}
              />
              <DcaVLabel
                label="Maximum Price"
                value={
                  direct
                    ? `${orderInfo?.maxPrice} ${sellCoin?.symbol} per ${buyCoin?.symbol}`
                    : `${orderInfo?.maxPriceResever} ${buyCoin?.symbol} per ${sellCoin?.symbol}`
                }
                direct={direct}
                onChangeDirect={() => setDirect(!direct)}
              />
              {currentTabVal == 'active' && (
                <DcaVLabel
                  label="Current Avg. Price"
                  value={
                    direct
                      ? `${orderInfo?.currentAvgPrice} ${sellCoin?.symbol} per ${buyCoin?.symbol}`
                      : `${orderInfo?.currentAvgPriceResever} ${buyCoin?.symbol} per ${sellCoin?.symbol}`
                  }
                  align="flex-end"
                  textAlign="right"
                  direct={direct}
                  onChangeDirect={() => setDirect(!direct)}
                />
              )}
              {currentTabVal !== 'active' && orderInfo?.currentAvgPrice !== '--' && (
                <DcaVLabel
                  label="Avg. Price"
                  value={
                    direct
                      ? `${orderInfo?.currentAvgPrice} ${sellCoin?.symbol} per ${buyCoin?.symbol}`
                      : `${orderInfo?.currentAvgPriceResever} ${buyCoin?.symbol} per ${sellCoin?.symbol}`
                  }
                  align="flex-end"
                  textAlign="right"
                  direct={direct}
                  onChangeDirect={() => setDirect(!direct)}
                />
              )}
              {currentTabVal == 'active' && <DcaVLabel label="Next Order (UTC)" value={orderInfo?.nextCycleAt} />}
              <DcaVLabel
                label="Created (UTC)"
                value={orderInfo?.createAt}
                align={currentTabVal == 'active' || (currentTabVal !== 'active' && orderInfo?.currentAvgPrice !== '--') ? 'flex-start' : 'flex-end'}
              />
            </HStack>
          </VStack>
        )}
      </Block>
    </VStack>
  )
}
interface DcaVLabelProps extends HTMLChakraProps<'div'> {
  label: string
  value: string | ReactNode
  direct?: boolean
  showDirectIcon?: boolean
  textAlign?: 'left' | 'right'
  onChangeDirect?: () => void
}
const DcaVLabel = ({ label, value, direct, onChangeDirect, textAlign = 'left', showDirectIcon = true, ...rest }: DcaVLabelProps) => {
  return (
    <VStack
      align="flex-start"
      flexDirection={{ base: 'row', lg: 'column' }}
      w={{ base: '100%', lg: 'unset' }}
      justify={{ base: 'space-between', lg: 'unset' }}
      {...rest}
    >
      {label !== '' && (
        <HStack h="12px">
          <Text fontSize="12px">{label}</Text>
          {onChangeDirect && showDirectIcon && <Icon xlinkHref="#icon-icon_swap1" svgW="14px" svgH="14px" ml="-6px" onClick={onChangeDirect} />}
        </HStack>
      )}
      {value !== '' && (
        <Box fontSize="12px" color="text_caption" textAlign={textAlign}>
          {value}
        </Box>
      )}
    </VStack>
  )
}

const WarningText = () => {
  return (
    <VStack p="12px 12px 0" align="flex-start">
      <HStack>
        <ErrorTips
          tips="The system tried to execute your order multiple times but did not get it through. This is possibly because:"
          type="warning"
          tipsFontSize="12px"
          p="0"
          bg="none"
          svgW="20px"
          svgH="20px"
        />
      </HStack>
      <Text color="primary_yellow" lineHeight="20px" fontSize="12px">
        - The market price is not in your required price range
      </Text>
      <Text color="primary_yellow" lineHeight="20px" fontSize="12px">
        - The market no longer exit
      </Text>
      <Text color="primary_yellow" lineHeight="20px" fontSize="12px">
        - The market is extremely volatile
      </Text>
      <Text color="primary_yellow" lineHeight="20px" fontSize="12px">
        This DCA will continue to be attempted and the estimated end date may be extended until your order is fully executed.
      </Text>
    </VStack>
  )
}
