import useTokenRank from '@/hooks/common/useTokenRank'
import useTransaction from '@/hooks/common/useTransaction'
import useDcaCancel from '@/hooks/dca/useDcaCancel'
import useExplorer from '@cetus/hooks/src/useExplorer'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { CommonTypeInfo, TransactionStatusType } from '@cetus/types'
import { CoinPairImage, Icon } from '@cetus/ui-kit'
import { formatNumber } from '@cetus/utils'
import { Button, HStack, Progress, Text, VStack } from '@chakra-ui/react'
import { useEffect, useState } from 'react'
import RangeValue from '../position/RangeValue'
import OverviewOrders from './OverviewOrders'

export default function DcaItem({ currentTabVal, orderInfo, handleRefresh }: { currentTabVal: string; orderInfo: any; handleRefresh: () => void }) {
  const { isApp } = useWindowWidth()
  const { inCoin: sellCoin, outCoin: buyCoin } = orderInfo
  const [isDetail, setIsDetail] = useState(false)
  const [isCloseLoading, setIsCloseLoading] = useState(false)

  useEffect(() => {
    setIsDetail(false)
  }, [currentTabVal])

  const { getTokenRank } = useTokenRank()
  const [pageDirect, setPageDirect] = useState(false)

  useEffect(() => {
    const direct = getTokenRank(sellCoin, buyCoin)
    console.log('üöÄ ~ useEffectDcaItem ~ direct:', buyCoin, sellCoin, direct)
    setPageDirect(!direct)
  }, [sellCoin?.coin_type, buyCoin?.coin_type])

  const { dcaCloseOrderPayload } = useDcaCancel()
  const { signAndExecuteTransaction } = useTransaction()
  const closeOrder = async () => {
    setIsCloseLoading(true)
    try {
      console.log('üöÄ ~ closeOrder ~ orderInfo:', orderInfo)
      const closeOrderParams = [
        {
          order_id: orderInfo?.orderID,
          in_coin_type: orderInfo?.inCoin.coin_type,
          out_coin_type: orderInfo?.outCoin.coin_type
        }
      ]
      console.log('üöÄüöÄüöÄ ~ file: dca-order-card.vue:280 ~ closeOrder ~ closeOrderParams:', closeOrderParams)
      const tx = await dcaCloseOrderPayload(closeOrderParams)
      const res = await signAndExecuteTransaction(tx, {
        getShowInfo: (status: TransactionStatusType) => {
          const info: CommonTypeInfo = {
            modalDescriptionText: `Closing DCA order`,
            toastTitleText: 'Closing DCA order'
          }
          if (status === 'success') {
            info.modalDescriptionText = 'DCA Order Closed'
            info.toastDescriptionContent = ''
            info.toastTitleText = 'DCA Order Closed'
          }

          return info
        }
      })
      console.log('üöÄ ~ closeOrder ~ res:', res)
      if (res) {
        // ÈáçÊñ∞ÊãøÊï∞ÊçÆ
        setTimeout(() => {
          handleRefresh()
        }, 2000)
      }
      setIsCloseLoading(false)
    } catch (error) {
      console.log('üöÄ ~ closeOrder ~ error:', error)
      setIsCloseLoading(false)
    }
  }
  const { getExplorerUrl } = useExplorer()
  return (
    <VStack w="100%" gap="12px" align="flex-start">
      <HStack w="100%" justify="space-between" flexDirection={{ base: 'column', lg: 'row' }} align={{ base: 'flex-start', lg: 'center' }}>
        <HStack>
          <CoinPairImage
            coinAIconUrl={sellCoin?.logo_url}
            coinBIconUrl={buyCoin?.logo_url}
            coinACoinType={sellCoin?.coin_type}
            coinBCoinType={buyCoin?.coin_type}
            w="28px"
            h="28px"
          />
          <Text color="text_caption">
            {sellCoin?.symbol} ‚Üí {buyCoin?.symbol}
          </Text>
          <Icon
            xlinkHref="#icon-icon_link1"
            onClick={() => {
              window.open(getExplorerUrl(orderInfo?.orderID, 'poolAddress'))
            }}
          />
        </HStack>
        {!isApp && (
          <BtnContent
            currentTabVal={currentTabVal}
            isCloseLoading={isCloseLoading}
            closeOrder={closeOrder}
            orderInfo={orderInfo}
            isDetail={isDetail}
            onClickDetail={() => setIsDetail(!isDetail)}
          />
        )}
      </HStack>
      <HStack>
        <Text color="text_caption" fontSize="12px">
          {formatNumber(orderInfo?.inDepositedOut)} {sellCoin?.symbol}
        </Text>
        <Text color="primary_gray" fontSize="12px">
          / {formatNumber(orderInfo?.inDeposited)} {sellCoin?.symbol}
        </Text>
        <Text color="primary_gray" fontSize="12px">
          ({orderInfo?.inBalanceRatio}%)
        </Text>
        <Progress
          w="50px"
          h="4px"
          value={orderInfo?.inBalanceRatio}
          bg="#282828"
          sx={{
            'div[role="progressbar"]': {
              bg: 'primary'
            }
          }}
        />
      </HStack>
      <HStack w="100%">
        <Text color="primary_gray" fontSize="12px">
          Price Range
        </Text>
        <RangeValue
          displayTokenA={buyCoin}
          displayTokenB={sellCoin}
          isRank={true}
          priceInfo={{
            minPrice: orderInfo?.minPrice,
            maxPrice: orderInfo?.maxPrice,
            minPriceResever: orderInfo?.minPriceResever,
            maxPriceResever: orderInfo?.maxPriceResever
          }}
          fontSize="12px"
        />
      </HStack>
      {isApp && (
        <BtnContent
          currentTabVal={currentTabVal}
          isCloseLoading={isCloseLoading}
          closeOrder={closeOrder}
          orderInfo={orderInfo}
          isDetail={isDetail}
          onClickDetail={() => setIsDetail(!isDetail)}
        />
      )}
      {isDetail && (
        <OverviewOrders
          pageDirect={pageDirect}
          isDetail={isDetail}
          currentTabVal={currentTabVal}
          orderInfo={orderInfo}
          handleRefresh={handleRefresh}
        />
      )}
    </VStack>
  )
}

const BtnContent = ({
  currentTabVal,
  isCloseLoading,
  closeOrder,
  orderInfo,
  isDetail,
  onClickDetail
}: {
  currentTabVal: string
  isCloseLoading: boolean
  closeOrder: () => void
  orderInfo: any
  isDetail: boolean
  onClickDetail: () => void
}) => {
  return (
    <HStack w={{ base: '100%', lg: 'unset' }} flexDirection={{ base: 'column', lg: 'row' }}>
      {currentTabVal == 'active' && (
        <Button
          w={{ base: '100%', lg: 'unset' }}
          isLoading={isCloseLoading}
          disabled={isCloseLoading}
          onClick={closeOrder}
          h="32px"
          minH="unset"
          p="0px 12px"
          fontSize="14px"
          borderRadius="8px"
          variant="outline"
        >
          {orderInfo?.outBalance <= 0 ? 'Close' : 'Close and Withdraw'}
        </Button>
      )}
      <Button
        w={{ base: '100%', lg: 'unset' }}
        h="32px"
        borderRadius="8px"
        minH="unset"
        p="0px 12px"
        fontSize="14px"
        variant="ghost"
        rightIcon={
          <Icon
            svgW="12px"
            svgH="12px"
            xlinkHref="#icon-icon_arrow"
            transform={isDetail ? 'rotate(180deg)' : 'rotate(0deg)'}
            transition="transform 0.5s"
            ml="-4px"
            mr="-4px"
          />
        }
        onClick={onClickDetail}
      >
        Details
      </Button>
    </HStack>
  )
}
