import useDcaStore from '@/store/dca'
import { useGetToken } from '@cetus/hooks/src/useToken'
import usePeripherySDKStore from '@cetus/stores/src/usePeripherySDKStore'
import useTokenSelectStore from '@cetus/stores/src/useTokenSelectStore'

export default function () {
  const { peripherySdk } = usePeripherySDKStore()
  const { getTokenInfo } = useGetToken()
  const { setFilterTokenLoading } = useTokenSelectStore()
  const { setInCoinWhiteList, setOutCoinWhiteList } = useDcaStore()
  // whitelist_mode = 0 ÂÖ≥Èó≠ÁôΩÂêçÂçïÊ®°Âºè
  // whitelist_mode = 1 Âè™ÂºÄÂêØin_coin
  // whitelist_mode = 2 Âè™ÂºÄÂêØout_coin
  // whitelist_mode = 3 ÂÖ®ÈÉ®ÂºÄÂêØ
  const getDcaCoinWhiteList = async (whitelist_mode: number) => {
    console.log('üöÄüöÄüöÄ ~ file: useGetDcaTokenList.ts:44 ~ getDcaCoinWhiteList ~ getDcaCoinWhiteList:')
    try {
      setFilterTokenLoading(true)
      const { inCoinList, outCoinList } = await peripherySdk.Dca.getDcaCoinWhiteList(whitelist_mode)
      console.log('üöÄüöÄüöÄ ~ file: useGetDcaTokenList.ts:18 ~ getDcaCoinWhiteList ~ outCoinList:', outCoinList)
      console.log('üöÄüöÄüöÄ ~ file: useGetDcaTokenList.ts:18 ~ getDcaCoinWhiteList ~ inCoinList:', inCoinList)
      const inList: any = []
      const outList: any = []
      for (let i = 0; i < inCoinList.length; i++) {
        const tokenInfo = await getTokenInfo(inCoinList[i])
        inList.push({
          ...tokenInfo,
          labels: tokenInfo?.labels?.length > 0 ? tokenInfo.labels : ''
        })
      }

      for (let i = 0; i < outCoinList.length; i++) {
        const tokenInfo = await getTokenInfo(outCoinList[i])
        outList.push({
          ...tokenInfo,
          labels: tokenInfo?.labels?.length > 0 ? tokenInfo.labels : ''
        })
      }

      console.log('üöÄüöÄüöÄ ~ file: useDca.ts:351 ~ getDcaCoinWhiteList ~ outList:', outList)
      console.log('üöÄüöÄüöÄ ~ file: useDca.ts:351 ~ getDcaCoinWhiteList ~ inList:', inList)
      setInCoinWhiteList(inList)
      setOutCoinWhiteList(outList)
      setFilterTokenLoading(false)
    } catch (error) {
      setInCoinWhiteList([])
      setOutCoinWhiteList([])
      setFilterTokenLoading(false)
      console.log('üöÄ ~ file: useGetDcaTokenList.ts:35 ~ getDcaCoinWhiteList ~ error:', error)
    }
  }
  return { getDcaCoinWhiteList }
}
